import { BpmnModelJsonHelper } from "../../ref";
import { ConditionContent } from './ConditionContent';

export class ComponentConditionContent extends ConditionContent {
    componentMetadataId: string;
    componentId: string;
    name: string;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.componentMetadataId = null;
        this.componentId = null;
        this.name = null;
    }

    public ConvertToJsonObject(): {} {
        let obj: {} = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "componentMetadataId", this.componentMetadataId);
        BpmnModelJsonHelper.SetAttributeValue(obj, "componentId", this.componentId);
        BpmnModelJsonHelper.SetAttributeValue(obj, "name", this.name);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.componentMetadataId = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "componentMetadataId");
        this.componentId = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "componentId");
        this.name = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "name");
    }
}