import { Entity, BpmnModelJsonHelper } from '../../ref';
import { IBillTrackingConfigEntity } from '../IBillTrackingConfigEntity';

export class BillTrackingConfigEntity extends Entity implements IBillTrackingConfigEntity {
    suCode: string;
    classTypeName: string;
    isEnabled: boolean = false;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.suCode = null;
        this.classTypeName = null;
        this.isEnabled = false;
    }

    public ConvertToJsonObject(): {} {
        let obj: {} = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "suCode", this.suCode);
        BpmnModelJsonHelper.SetAttributeValue(obj, "classTypeName", this.classTypeName);
        BpmnModelJsonHelper.SetAttributeValue(obj, "isEnabled", this.isEnabled);

        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.suCode = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "suCode");
        this.classTypeName = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "classTypeName");
        this.isEnabled = BpmnModelJsonHelper.GetAttributeValueAsBoolean(obj, "isEnabled");
    }
}