import { BillSchemaField, BillSchemaSubField } from "@edp-aif/common-api";

declare module '@edp-aif/common-api/lib/billkit' {
    export interface BillSchemaFieldBase {
        fieldPath?: string;
        displayName?: string;
        order?: number;
        /**
         * fullPath = "#" + tableName + "." + fieldPath
         */
        fullPath?: string;
        captionPath?: string;
        i18nCols?: {
            colI18nValues?: any;
        };
        formatInfo?: {
            dateFormatter?: string;
            scale?: number;
            scaleExpression?: string;
            enableScaleExpression?: boolean;
            stringMapFormatter?: any;
        };
    }
}

export const fullPathPrefix: string = "#";

export class BillTrackerSchemaTable {
    code: string;
    caption: string;
    displayName: string;
    order: number;
    show: boolean;
    fields: (BillSchemaField | BillSchemaSubField)[];
    i18nCols?: {
        colI18nValues?: any;
    };
    maxDisplayNumber: number;

    /**
     * 自定义JSON序列化方法
     * @returns 
     */
    toJSON() {
        const fields = this.fields && this.fields.map((originF) => {
            let sf = new BillSchemaField();
            // sf.fullPath = originF.fullPath;
            sf.fieldPath = originF.fieldPath;
            sf.fieldName = originF.fieldName;
            sf.caption = originF.caption;
            sf.displayName = originF.displayName;
            sf.order = originF.order;
            sf.i18nCols = originF.i18nCols;
            sf.formatInfo = originF.formatInfo;

            return sf;
        });

        const obj = {
            code: this.code,
            caption: this.caption,
            displayName: this.displayName,
            order: this.order,
            show: this.show,
            fields: fields,
            i18nCols: this.i18nCols,
            maxDisplayNumber: this.maxDisplayNumber,
        };

        return obj;
    }
}