import { BpmnModelJsonHelper } from "../../ref";
import { RtSlimBillCreationProcessEntity } from "./RtSlimBillCreationProcessEntity";
import { IRtSlimBillCreationProcessEntityEx } from "../IRtSlimBillCreationProcessEntityEx";
import { RtBillCreationProcessEntity } from "../../entity/impl/RtBillCreationProcessEntity";

/**
 * 单据生单流简化实体扩展类。
 */
export class RtSlimBillCreationProcessEntityEx extends RtSlimBillCreationProcessEntity implements IRtSlimBillCreationProcessEntityEx {
    name: string;
    description: string;
    upBillVoName: string;
    downBillVoName: string;

    public RtSlimBillCreationProcessEntityEx(entity?: RtBillCreationProcessEntity) {
        if (entity != null) {
            this.id = entity.id;
            this.code = entity.code;
            this.upBillVoId = entity.upBillVoId;
            this.upBillVoMetadataId = entity.upBillVoMetadataId;
            this.downBillVoId = entity.downBillVoId;
            this.downBillVoMetadataId = entity.downBillVoMetadataId;
            this.downBillVoProxyMetadataId = entity.downBillVoProxyMetadataId;
            this.downBillVoProxyId = entity.downBillVoProxyId;
            this.eventRouterConditionsId = entity.eventRouterConditionsId;
            this.mappingConditionsId = entity.mappingConditionsId;
            this.formSettingsId = entity.formSettingsId;
            this.bizEventId = entity.bizEventId;
            this.creationRuleId = entity.creationRuleId;
            this.isEnabled = entity.isEnabled;
            this.isSupportAutomatinFlow = entity.isSupportAutomatinFlow;

            this.name = entity.name;
            this.description = entity.description;
        }
    }

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();
        this.name = null;
        this.description = null;
        this.upBillVoName = null;
        this.downBillVoName = null;
    }

    public ConvertToJsonObject(): {} {
        let obj: {} = super.ConvertToJsonObject();
        BpmnModelJsonHelper.SetAttributeValue(obj, "name", this.name);
        BpmnModelJsonHelper.SetAttributeValue(obj, "description", this.description);
        BpmnModelJsonHelper.SetAttributeValue(obj, "upBillVoName", this.upBillVoName);
        BpmnModelJsonHelper.SetAttributeValue(obj, "downBillVoName", this.downBillVoName);
        return obj;
    }

    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);
        this.name = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "name");
        this.description = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "description");
        this.upBillVoName = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "upBillVoName");
        this.downBillVoName = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "downBillVoName");
    }
}
