import { BpmnModelJsonHelper } from '../../ref';
import { ComponentContent } from './ComponentContent';

export class MappingComponentContent extends ComponentContent {
    mappingConfig: string;


    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.mappingConfig = null;
    }


    public ConvertToJsonObject(): {} {
        let obj: {} = super.ConvertToJsonObject();

        BpmnModelJsonHelper.SetAttributeValue(obj, "mappingConfig", this.mappingConfig);

        return obj;
    }


    public LoadFromJsonObject(obj: {}): void {
        super.LoadFromJsonObject(obj);

        this.mappingConfig = BpmnModelJsonHelper.GetAttributeValueAsString(obj, "mappingConfig");
    }
}
